/*------------------------------------*\
    NO JS

    A simple mixin that will allow consistent no JS related styles 
\*------------------------------------*/
@mixin no-js($is-parent: false) {

    @if $is-parent == true {

        &.no-js {
            @content;
        }
    }
    @else {

        .no-js & {
            @content;
        }
    }
}



