%button-reset {
    display: inline;
    border: none;
    padding: 0;
    margin: 0;
    text-decoration: none;
    background: transparent;
    color: $body-color;
    font-family: $font-family-base;
    font-size: 1rem;
    line-height: $line-height-base;
    cursor: pointer;
    text-align: left;
    -webkit-appearance: none;
    -moz-appearance: none;
}

%button-reset--hover {
    background: transparent;
}

%button-reset--focus {
    outline: none;
}



