  .breakpoint-helper {
    background: #FF851B;
    color: #fff;
  }
}

@media (min-width: 768px) {
  .breakpoint-helper {
    background: #01FF70;
    color: #fff;
  }
}

@media (min-width: 992px) {
  .breakpoint-helper {
    background: #B10DC9;
    color: #fff;
  }
}

@media (min-width: 1270px) {
  .breakpoint-helper {
    background: #0074D9;
    color: #fff;
  }
}

@media (min-width: 1600px) {
  .breakpoint-helper {
    background: #39CCCC;
    color: #fff;
  }
}

@media (min-width: 2080px) {
  .breakpoint-helper {
    background: #FF4136;
    color: #fff;
  }
}

.breakpoint-helper:after {
  display: block;
  content: "XXS";
}

@media (min-width: 620px) {
  .breakpoint-helper:after {
    content: "XS";
  }
}

@media (min-width: 768px) {
  .breakpoint-helper:after {
    content: "SM";
  }
}

@media (min-width: 992px) {
  .breakpoint-helper:after {
    content: "MD";
  }
}

