  .breakpoint-helper:after {
    content: "LG";
  }
}

@media (min-width: 1600px) {
  .breakpoint-helper:after {
    content: "XL";
  }
}

@media (min-width: 2080px) {
  .breakpoint-helper:after {
    content: "XXL";
  }
}

